.upload-section {
  padding: 40px;
}

.upload-content h2 {
  text-align: center;
  margin-bottom: 30px;
  color: #333;
  font-size: 1.8rem;
  font-weight: 600;
}

.dropzone {
  border: 3px dashed #ddd;
  border-radius: 12px;
  padding: 60px 20px;
  text-align: center;
  cursor: pointer;
  transition: all 0.3s ease;
  background: #fafafa;
}

.dropzone:hover,
.dropzone.active {
  border-color: #667eea;
  background: #f0f4ff;
  transform: translateY(-2px);
}

.upload-icon {
  width: 48px;
  height: 48px;
  color: #667eea;
  margin-bottom: 16px;
}

.upload-text {
  font-size: 1.2rem;
  color: #333;
  margin-bottom: 8px;
  font-weight: 500;
}

.upload-hint {
  color: #666;
  font-size: 0.9rem;
}

.preview-section {
  text-align: center;
}

.preview-container {
  position: relative;
  display: inline-block;
  margin-bottom: 30px;
}

.preview-image {
  max-width: 100%;
  max-height: 400px;
  border-radius: 12px;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.15);
  transition: transform 0.3s ease;
}

.preview-image:hover {
  transform: scale(1.02);
}

.remove-btn {
  position: absolute;
  top: -10px;
  right: -10px;
  background: #ff4757;
  color: white;
  border: none;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 12px rgba(255, 71, 87, 0.3);
}

.remove-btn:hover {
  background: #ff3742;
  transform: scale(1.1);
}

.action-buttons {
  display: flex;
  justify-content: center;
  gap: 16px;
}

.search-btn {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 1.1rem;
  padding: 16px 32px;
}

.error-message {
  margin-top: 20px;
  padding: 16px;
  background: #ffe6e6;
  border: 1px solid #ffcccc;
  border-radius: 8px;
  color: #d63031;
  text-align: center;
}

@media (max-width: 768px) {
  .upload-section {
    padding: 20px;
  }
  
  .dropzone {
    padding: 40px 15px;
  }
  
  .upload-content h2 {
    font-size: 1.5rem;
  }
  
  .search-btn {
    width: 100%;
    justify-content: center;
  }
}